<template>
  <div class="container todolist">
    <h1 class="text-center">備忘清單</h1>
    <p class="">今天日期: {{today}} </p>
    <div class="input-group mb-3">
      <!-- 透過Bootstrap的input-group-prepend、input-group-append來將內容呈現在一行上 -->
      <div class="input-group-prepend">
        <span class="input-group-text" id="basic-addon1">待辦事項</span>
      </div>
      <input type="text" class="form-control" placeholder="準備要做的任務" v-model="data.newTodo" @keyup.enter="addTodolist">
      <div class="input-group-append">
        <button class="btn btn-primary" type="button" @click="addTodolist">新增</button>
      </div>
      <!-- <div>{{updateData()}}</div> -->
    </div>
    <!-- 卡片元件的清單 -->
    <div class="card text-center">
      <div class="card-header">
        <!-- 清單的三種狀態分頁切換 -->
        <ul class="nav nav-tabs">
          <li class="nav-item">
            <a class="nav-link" :class="{'active': visibility === 'all'}" @click="visibility = 'all'" href="#">全部</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" :class="{'active': visibility === 'running'}" @click="visibility = 'running'" href="#">進行中</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" :class="{'active': visibility === 'completed'}" @click="visibility = 'completed'" href="#">已完成</a>
          </li>
        </ul>
      </div>
      <!-- 清單的內容 -->
      <div class="card-body">
        <ul class="list-group list-group-flush">
          <li class="list-group-item" v-for="(item, key) in filterTodoList" v-bind:key="key">
            <div class="d-flex form-check" v-if = "item.id !== data.cacheTodo.id">
              <input type="checkbox" class="form-check-input" v-model="item.completed" :id="item.id">
              <label :class="{'completed': item.completed}" class="form-check-label" :for="item.id" @dblclick="editTodo(item)">{{item.title}}</label>
              <span class="ml-auto">
                <a class="ml-auto mr-2" @click.prevent="editTodo(item)" href="#">修改</a>
                <span class="mr-2"> | </span>
                <button type="button" class="close" aria-label="Close">
                  <span @click="clearTodo(item, key)" aria-hidden="true"> &times;</span>
                </button>
              </span>
            </div>
            <input type="text" class="form-control"
            v-model="data.cacheTitle"
            @keyup.esc="cancelEdit"
            @keyup.enter="doneEdit(item)"
            v-if = "item.id === data.cacheTodo.id">
          </li>
        </ul>
      </div>
      <!-- 清單的頁尾 -->
      <div class="card-footer d-flex justify-content-between">
        <span>還有 {{runevent()}} 筆任務未完成</span>
        <a @click.prevent="clearAllTodos" href="#">清除所有任務</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TodoList',
  data () {
    return {
      data: {
        newTodo: '',
        TodoList: [
          {
            id: '123456',
            title: 'HelloWorld',
            completed: false
          }
        ],
        cacheTodo: {},
        cacheTitle: ''
      },
      visibility: 'all',
      today: ''
    }
  },
  created () {
    // console.log(JSON.parse(localStorage.getItem('List')))
    const getList = JSON.parse(localStorage.getItem('List'))
    this.data.TodoList = getList || []
    // 取得現在時間
    const date = new Date()
    date.getFullDate = function () {
      // console.log(this)
      const dd = String(this.getDate())
      const mm = String(this.getMonth() + 1)
      const yyyy = this.getFullYear()
      const today = yyyy + '/' + mm + '/' + dd
      return today
    }
    const today = date.getFullDate()
    console.log(today)
    this.today = today
  },
  computed: {
    // 設計在不同狀態時能取得的資料
    filterTodoList () {
      const vm = this
      if (vm.visibility === 'all') {
        return vm.data.TodoList
      } else if (vm.visibility === 'running') {
        const newTodos = []
        vm.data.TodoList.forEach(function (item) {
          if (item.completed === false) {
            newTodos.push(item)
          }
        })
        return newTodos
      } else if (vm.visibility === 'completed') {
        const newTodos = []
        vm.data.TodoList.forEach(function (item) {
          if (item.completed === true) {
            newTodos.push(item)
          }
        })
        return newTodos
      }
      return vm.data.TodoList
    }
  },
  methods: {
    addTodolist () {
      const vm = this
      const timestamp = Math.floor(Date.now())
      const value = vm.data.newTodo.trim()
      if (value !== '') {
        vm.data.TodoList.push({
          id: timestamp,
          title: vm.data.newTodo,
          completed: false
        })
        vm.data.newTodo = ''
        localStorage.setItem('List', JSON.stringify(vm.data.TodoList))
      }
    },
    editTodo (item) {
      this.data.cacheTodo = item
      this.data.cacheTitle = item.title
    },
    doneEdit (item) {
      item.title = this.data.cacheTitle
      this.data.cacheTitle = ''
      this.data.cacheTodo = {}
    },
    cancelEdit () {
      this.data.cacheTodo = {}
    },
    clearTodo (item, key) {
      const vm = this
      console.log(item.id)
      console.log(key)
      vm.data.TodoList.forEach(function (arr) {
        if (item.id === arr.id) {
          vm.data.TodoList.splice(key, 1)
          localStorage.setItem('List', JSON.stringify(vm.data.TodoList))
        }
      })
    },
    clearAllTodos () {
      const vm = this
      vm.data.TodoList = []
      localStorage.setItem('List', JSON.stringify(vm.data.TodoList))
    },
    runevent () {
      let count = 0
      this.data.TodoList.forEach(function (item) {
        if (item.completed === false) {
          count += 1
        }
      })
      return count
    },
    saveData () {
      localStorage.setItem('List', JSON.stringify(this.data.TodoList))
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.completed{
  text-decoration: line-through;
}
</style>



